a {
  text-decoration: none;
  color: #ff00c8;
}

a:link {
  text-decoration: none;
  color: #ff00c8;
}

a:visited {
  text-decoration: none;
  color: #ff00c8;
}

a:hover {
  text-decoration: underline;
  color: #ff00c8;
}

a:active {
  text-decoration: underline;
  color: #ff00c8;
}

.aboutText {
  display: inline-block;
  max-width: 800px;
  padding-left: 10px;
  padding-right: 10px;
  text-align: justify;
}

.profilePictureWrapper {
  margin-bottom: 10px;
}

.profilePicture {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  border: solid 3px pink;
}

.paragraph {
  margin-bottom: 30px !important;
}

.caption {
  font-style: italic !important;
  margin-bottom: 30px !important;
}

.image {
  width: 100% !important;
}

.project-container {
  text-align: center;
  width: 100%;
}

.project-container .project-header-image {
  margin-top: 15px;
}

.project-container .project-source-icon {
  display: inline-block;
  padding-left: 15px;
}

.project-container .project-component-container {
  text-align: justify;
  display: inline-block;
  max-width: 800px;
  margin-bottom: 30px;
}

.project-container .project-component-padding {
  padding: 10px;
}

.back-button {
  position: absolute !important;
  top: 10px;
  left: 10px;
}

.back-button-mobile {
  margin-bottom: 30px !important;
}

body {
  background: "white";
  margin-left: 0px;
  padding-left: 0px;
}

